%hide {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

%modal{
  position: relative;
  transform: scale(1);
  background: #ffffff;
  padding: 40px;
  margin: 0 auto;
  transition: transform 500ms cubic-bezier(0.4, 0, 0.2, 1);
}